#searchform {
  margin-top: 30px;

  div {
    display: flex;
    align-items: center;
    justify-content: space-between;

    input[type='text'] {
      border-radius: 50px;
      padding: 18px 15px;
    }

    input[type='submit'] {
      font-weight: 700;
      width: 230px;

      &:hover,
      &:focus {
        cursor: pointer;
      }
    }
  }
}
