// Blog filter
.blog-filter {
  background-color: $green-7;
  padding: 20px 20px;

  .reset-link {
    width: 100%;
    height: 100%;
    text-align: center;
    align-self: baseline;
    white-space: nowrap;

    > a {
      font-size: 16px;
      line-height: 24px;

      &:hover {
        text-decoration: underline;
        cursor: pointer;
      }
    }
  }

  .btn-filter-rst {
    font-size: 14px;
    color: $blue-3;
    border: 1px solid $blue-3;
    padding: 0px 10px;
    border-radius: 10px;
    width: auto;
    margin: 10px 0;
    position: relative;
    right: 0;
    bottom: 0;
    font-weight: 600;
    font-family: $redhat;
    width: 100%;
    text-align: center;
  }

  &.filter-w-search {
    // display: flex;

    .col-sm-12 {
      width: 100%;
    }

    #searchform {
      padding: 0;
      margin-top: 0;
      font-size: 16px;
      // width: 17vw;

      &.input-group,
      .input-group {
        position: relative;

        .input-group-append {
          position: absolute;
          right: 25px;
          top: 0;
          height: 100%;
          svg {
            color: $blue-1;
            fill: $blue-1;
            transition: all 0.3s ease-in-out;
          }
          button {
            &:hover,
            &:focus {
              svg {
                color: $green-1;
                fill: $green-1;
              }
            }
          }
        }
      }

      input[type='text'] {
        min-width: 100% !important;
        width: 100% !important;
        padding: 16px 20px;
        padding-right: 50px;
        width: 280px;
        font-size: 16px;
        background-color: $white;
        border: none;
        color: $blue-1;
        font-weight: 600;
        appearance: none;
        border-radius: 50px;
        margin-right: 0 !important;

        &::placeholder {
          font-size: 16px;
          opacity: 1;
          font-weight: 600;
          color: $blue-1;
        }

        &:focus {
          outline: 0;
        }
      }
    }
  }

  .filter-checkboxes {
    &.row {
      align-items: flex-start;
      &.w-rst-btn {
        padding-bottom: 35px;
      }
    }
  }

  .select-wrap {
    position: relative;

    select {
      background-color: $white;
      border: none;
      color: $blue-1;
      font-weight: 600;
      appearance: none;
      width: 250px;
      border-radius: 50px;
      font-size: 12px;
      padding: 15px 40px;
      margin-bottom: 10px;
    }
  }

  .search-separator {
    margin: 30px 0;
  }

  .blog-search-form {
    &:hover,
    &:focus {
      cursor: pointer;
    }
  }
}
