// Newsletter
.newsletter-signup-wrap {
  background-color: $blue-1;
  padding: 30px;

  .newsletter-inner-holder {
    position: relative;
    z-index: 4;
  }

  .newsletter-description {
    margin-top: 20px;
    margin-bottom: 25px;
  }

  .newsletter-background-img {
    background-size: cover;
    background-position: 70% 50%;
  }
}
