img[data-src] {
  opacity: 0;
  transition: opacity 0.25s ease-in-out;
  will-change: opacity;
}

/* appear animation */
img[data-src][src] {
  opacity: 1;
}

// Related articles - single article page
.related-articles {
  margin-top: 60px;
  padding-bottom: 50px;

  > div {
    transition: opacity 0.3s ease;
  }

  .related-articles-wrap {
    padding: 0;
    list-style: none;
    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-rows: 1fr;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;

    &.blog-grid-wrap {
      justify-content: flex-start;
    }

    .related-post-item {
      width: 100%;
      margin-top: 50px;

      &:not(:first-of-type) {
        margin-top: 50px;
      }

      h3,
      .h3 {
        width: 93%;
      }

      .single-article-title {
        text-transform: none;

        a {
          color: $blue-1;
        }
      }

      .related-reading-time {
        color: $gray-22;
      }
    }
  }

  .related-meta {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin: 20px 0;

    .post-date {
      min-width: 150px;
      margin-top: 20px;
    }
  }
}

.blog-post-thumbnail {
  object-fit: cover;
  margin-left: auto;
  margin-right: auto;
  width: 490px;
  height: 300px;
}

.reading-time {
  a {
    color: $blue-1;
  }
}

// Blog page
.blog-banner {
  background: $blue-1;
  background: rgba(0, 0, 0, 0%);
  p,
  h1 {
    text-shadow: 1px 1px #151515;
  }
}

// Blog holder
.blog-holder {
  padding-top: 45px;
}

// Newsletter
.newsletter-signup-wrap {
  background-color: $blue-1;
  padding: 30px;

  .newsletter-inner-holder {
    position: relative;
    z-index: 4;
  }

  .newsletter-description {
    margin-top: 20px;
    margin-bottom: 25px;
  }

  .newsletter-background-img {
    background-size: cover;
  }
}

.posts-filter {
  select {
    padding: 10px;
    font-size: 16px;
  }
}

// Blog pagination
.blog-pagination {
  text-align: center;
  padding-bottom: 40px;

  a {
    font-size: 21px;
    color: $blue-1;
    display: inline-block;
    padding: 0 10px;

    &.active {
      color: $green-1;
      font-size: $contentPadding;
      font-weight: 700;
    }
  }

  span {
    color: $blue-1;
    font-size: 20px;
  }
}

.blog-contact-item {
  a {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-bottom: 1px solid $blue-1;
    padding: 20px 0;
    text-decoration: none !important;
    font-size: 24px;

    &:hover,
    &:focus {
      text-decoration: underline !important;
    }

    p {
      color: $blue-1 !important;
      font-family: $redhat !important;
      transition: color 0.3s ease;
      &:hover,
      &:focus {
        color: $blue-1 !important;
      }
    }

    &:hover,
    &:focus,
    &:focus-within {
      color: $blue-1;

      p {
        color: $blue-1 !important;
        text-decoration: none !important;
      }
    }

    .icon {
      margin-right: 30px;
      display: flex;
      align-items: center;
    }
  }
}

// Search blog form and results page

#searchform {
  margin-top: 30px;

  div {
    display: flex;
    align-items: center;
    justify-content: space-between;

    input[type='text'] {
      border-radius: 50px;
      padding: 18px 15px;
    }

    input[type='submit'] {
      font-weight: 700;
      width: 230px;

      &:hover,
      &:focus {
        cursor: pointer;
      }
    }
  }
}

.small-thumbnail {
  img {
    object-fit: cover;
    width: 490px;
    height: 300px;
    margin-left: auto;
    margin-right: auto;
  }
}

.archive-pagination {
  text-align: center;
  width: 100%;
  padding-bottom: 40px;

  span {
    font-size: 21px;
    color: $blue-1;
    display: inline-block;
    padding: 0 10px;

    &.current {
      color: $green-1;
      font-size: $contentPadding;
      font-weight: 700;
    }
  }

  .page-numbers {
    color: $blue-1;
    font-size: 20px;
    margin: 0 10px;

    &.prev {
      display: none !important;
    }

    &.next {
      display: none !important;
    }
  }
}

// Search results
.back-to-blog {
  display: flex;
  align-items: center;
  margin-top: 40px;

  &:hover,
  &:focus {
    img {
      margin-right: 10px;
      transition: margin-right 0.3s ease;
    }
  }

  img {
    margin-right: 20px;
    transform: rotate(-180deg);
    transition: margin-right 0.3s ease;
  }
}
