// Single article
.post-single-category {
  border: 1px solid $blue-1;
  color: $blue-1;
  font-size: 12px;
  font-weight: 500;
  border-radius: 20px;
  padding: 5px 9px;
  text-transform: none;
  display: inline-block;
  background-color: transparent;
  transition: background-color 0.3s ease;
  text-decoration: none !important;

  &:hover,
  &:focus {
    background-color: $green-1;
    transition: background-color 0.3s ease;
  }
}

.single-article-section {
  margin-left: 0;
  margin-right: 0;
}

.single-article-newsletter {
  margin-top: 30px !important;
}

.sk-blog-content {
  .sk-block {
    padding-bottom: 0px;
    padding-top: 0px;
    margin-bottom:30px;

    .container {
      padding-left: 0px;
      padding-right: 0px;
    }
  }

  .blog-content {
    & > div {
      & > picture {
        img {
          margin-bottom:30px;
        }
      }
    }

    iframe {
      margin-bottom:30px;
    }
  }

  .full-width-bg {
    background-color: $fw-bg;
    padding-left: 30px;
    padding-right: 30px;
  }

  .svg-img {
    padding-bottom: 15px;
    padding-top: 15px;
  }

  .container {
    max-width: unset;
    padding-left: 0;
    padding-right: 0;
  }
  .article {
    &-body {
      .article-content {
        width: 100%;

        .article-text,
        .table-of-contents {
          width: 100%;
          padding-left: 0;
          padding-right: 0;
        }

        .px-lg-4 {
          padding: 0 !important;
        }
        // Article meta
        .article-meta {
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          border-bottom: 1px solid $gray-1;
          margin-top: 30px;
          padding-bottom: 20px;

          p {
            margin-bottom: 0;
          }

          .meta-separator {
            margin: 0 10px;
          }
        }

        .article-featured-img {
          width: 100%;
        }

        .photo-credit {
          font-style: italic;
          text-align: right;
          margin-top: 15px;
        }

        // Article main contant
        .article-main-content {
          margin-top: $contentPadding;

          .table-of-contents {
            padding-top: 0;

            .toc-title {
              font-family: $libre !important;
              font-size: 24px !important;
              font-weight: 600 !important;
              line-height: 36px !important;
            }

            p {
              margin-left: 18px;
            }

            strong,
            b {
              font-weight: normal !important;
            }

            .content-bullets > ol {
              margin-top: 0;
              margin-left: 0;
              padding-left: 17px;

              li {
                line-height: 30px;

                > ol {
                  padding-left: 15px;
                  list-style-type: disc;
                  margin-top: 20px;

                  li > ol {
                    list-style: circle;
                    padding-left: 15px;
                    margin-top: 20px;

                    li > ol {
                      list-style: square;
                      padding-left: 15px;
                      margin-top: 20px;

                      li > ol {
                        list-style: decimal;
                        padding-left: 15px;
                        margin-top: 20px;

                        li > ol {
                          list-style: lower-alpha;
                          padding-left: 15px;
                          margin-top: 20px;

                          li > ol {
                            list-style: upper-alpha;
                            padding-left: 15px;
                            margin-top: 20px;
                          }
                        }
                      }
                    }
                  }
                }
              }

              display: inline-block;

              li {
                line-height: 30px;
              }
            }
          }

          .article-text {
            b,
            strong,
            b {
              font-weight: bold;
            }

            h1,
            .h1,
            h2,
            .h2,
            h3,
            .h3,
            h4,
            .h4,
            h5,
            .h5,
            h6,
            .h6 {
              &:not(:first-of-type) {
                margin-top: 1rem;
              }
            }
          }

          .icon-columns.icon-sidebar {
            display: flex;
            flex-direction: row !important;
            justify-content: space-between !important;

            &::before,
            &::after {
              content: '';
              position: absolute;
              top: 0;
              bottom: 0;
              width: 2px;
              background: #939598;
            }
            &::before {
              left: 33%;
            }
            &::after {
              right: 33%;
            }
          }

          thead tr th, tbody tr td {
            padding-right: 15px;
          }
        }
      }

      .wp-block-image.size-full {
        img {
          width: 100%;
        }
      }

      // Article aside
      .article-aside {
        .sk-sticky {
          z-index: 1;
        }

        padding-top: 25px;
        width: 100%;

        a {
          display: block;
          color: $blue-1;
          transition: color 0.3s ease;
          margin-bottom: 20px;

          &:hover,
          &:focus {
            color: $red-1;
            transition: color 0.3s ease;
          }
        }
      }
    }

    // Article author
    &-author {
      background-color: $gray-21;
      margin-right: 0;
      margin-left: 0;
      padding: 30px;
      margin-bottom: 30px;

      .author-image {
        margin-bottom: 25px;

        img {
          border-radius: 50%;
          width: 200px;
        }
      }

      .author-info {
        .author-name {
          margin-bottom: 20px;
        }
      }
    }
  }
}

.sk-case-study,
.sk-blog-content {
  .article-body {
    margin-bottom: 100px;

    & > .article-content {
      .blog-content {
        a:not(.btn) {
          font-weight: 600;
          text-decoration: underline;
        }

        ul, ol {
          line-height: 1.55em;
          font-size: 18px;

          li:not(:last-child){
            margin-bottom:10px;
          }
        }
      }
    }

    .table-of-contents {
      .toc-title {
        font-family: $libre !important;
        font-size: 24px !important;
        font-weight: 600 !important;
        line-height: 36px !important;
      }
      .anchor-link {
        font-family: $libre !important;
        font-size: 18px !important;
        font-weight: 400 !important;
        line-height: 30px !important;
        transition: all 100ms ease-in !important;

        &:hover,
        &:focus {
          text-decoration: underline !important;
        }

        &.active {
          transition: all 300ms ease-in-out !important;
          font-weight: 600 !important;
        }
      }
    }

    .is-layout-flex {
      display: block !important;

      .wp-block-column {
        display: flex;
        flex-direction: column;

        &:first-of-type {
          padding-bottom: 15px;
        }
        &:last-of-type {
          padding-top: 15px;
        }
      }
    }
  }
  .article-aside {
    .sk-sticky {
      z-index: 1;
    }

    .icon-columns {
      .icons--item {
        .list-image {
          position: relative;
          margin-bottom: 10px;

          &::after {
            content: '';
            display: block;

            position: absolute;

            background-position: center center;
            background-size: 2px 70%;
            background-repeat: no-repeat;
            background-image: url($ROOT_PATH + 'media/svgs/separator.svg');

            background-color: #939598;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 1.2px;
          }
        }
        &:last-of-type {
          .list-image {
            margin-bottom: 0px;
            &::after {
              display: none;
            }
          }
        }
      }
    }
  }
  .sk-slider {
    .icons--item,
    .img--wrap,
    .img--wrap img {
      width: 100%;
      height: 175px;
    }

    .img--wrap {
      img {
        object-fit: contain;
      }
    }
  }
}

.newsletter-inner-holder {
  .subscribe-newsletter-form .hbspt-form form {
    align-items: flex-start;

    .hs_submit.hs-submit {
      margin-top: 20px;
    }
  }
}
