.related-post-thumb {
  width: 100%;
  height: 350px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.related-articles {
  margin-top: 60px;
  padding-bottom: 50px;

  .related-articles-wrap {
    padding: 0;
    list-style: none;
    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-rows: 1fr;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;

    &.blog-grid-wrap {
      justify-content: flex-start;
    }

    .c-blue-1 a {
      text-decoration: none !important;

      &:hover {
        text-decoration: underline !important;
      }
    }

    .related-post-item {
      margin-top: 50px;
      width: 100%;

      &:not(:first-of-type) {
        margin-top: 50px;
      }

      h3,
      .h3 {
        width: 93%;
      }

      .single-article-title {
        text-transform: none;

        a {
          color: $blue-1;
        }
      }

      .related-reading-time {
        color: $gray-22;
      }
    }
  }

  .related-meta {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin: 20px 0 0;

    .post-date {
      min-width: 150px;
      margin-top: 20px;
    }
  }

  .related-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }
}
