:host {
  position: sticky;
  display: block; // this is the same as shown above
  top: 0;
}

:focus {
  outline: 3px dotted $green-4;
  outline-offset: 7px;
}

:focus:not(:focus-visible) {
  outline: none;
}

:focus-visible {
  outline: 3px dotted $green-4;
  outline-offset: 7px;
}

cite {
  font-style: inherit;
}

.single-post.f0 {
  font-size: initial !important;
}

.ml-0 {
  margin-left: 0 !important;
}
.pl-0 {
  padding-left: 0 !important;
}
