.article-content {
  .article-share {
    display: flex;
    align-items: top;
    margin: 20px 0;

    .social-item {
      flex: 0 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      border-radius: 50%;
      transition: background-color 0.5s $easeOutSine;
      background-color: $green-1;
      width: 35px;
      height: 35px;
      margin: 0 9.5px;
      color: $white;
      
      &:hover,
      &:focus {
        background-color: $blue-1;

        .x--logo,
        .fb--logo,
        .linkedin--logo {
          fill: white;
        }
      }

      .share-x,
      .share-fb,
      .share-linkedin {
        width: 100%;
        height: auto;
        transition: fill 0.5s $easeOutSine;
      }
    }
  }
}
