.timber-block .sk-blog-block.blog-cta {
  margin-bottom: $contentPadding;
}
.timber-block .sk-blog-block.blog-cta,
.sk-blog-block.blog-cta {
  position: relative;
  background-color: $blue-1;
  min-height: 229px;
  .inner-wrapper {
    z-index: 1;
    height: 100%;
    position: relative;
    gap: 10px;

    .cta-content {
      padding: 24px 34px;
      min-height: 229px;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;

      h2 {
        margin-bottom: 0.85rem;
        font-size: 32px;
        line-height: 40px;
      }

      *,
      p {
        margin-bottom: 0.85rem;

        :empty {
          display: none !important;
        }
      }
      .btn {
        margin-bottom: 0;
        width: fit-content;
        font-family: $redhat;
        font-size: 16px;
        line-height: 20px;
        font-weight: 700;
        text-decoration: none !important;
      }
    }

    .cta-form {
      padding: 0 0 24px 24px;
    }
  }

  &.w-svg {
    @media screen and ($mobile) {
      height: fit-content;
      min-height: fit-content;
    }
    &::before {
      content: '';
      display: block;
      background-image: url($ROOT_PATH + 'media/svgs/blog-cta.svg');
      background-position: bottom right;
      background-repeat: no-repeat;
      background-size: contain;
      width: 100%;
      height: 85%;
      position: absolute;
      bottom: 0;
      right: 0;
      z-index: 0;
    }
  }

  &.w-form {
    .cta-content {
      padding-bottom: 0;

      :last-child {
        margin-bottom: 0;
      }
    }
  }

  &.w-image {
    .cta-content {
      max-width: 60%;
    }
    .image-block {
      picture {
        display: block;
      }
      img {
        height: 100%;
        object-fit: cover;
        width: 100%;
      }

      position: absolute;
      width: 35%;
      height: 100%;
      right: -1px;
      mask: url($ROOT_PATH + 'media/svgs/blog-cta-bg.svg') no-repeat;
      position: absolute;
      mask-position: left;
      mask-size: cover !important;
      right: -1px;
      width: 40%;
      height: 100%;
      left: unset;

      img {
        position: absolute;
        right: 0;
      }

      mask-size: 100% 100% !important;
    }
  }

  .newsletter-form {
    .hbspt-form {
      padding: 0 0 0 0;
      background-color: transparent;

      form {
        margin-top: 0;
      }

      .hs-error-msg.hs-main-font-element,
      .hs-error-msgs .hs-main-font-element {
        color: #17fcc4;
      }

      .hs_submit {
        width: 100%;
        .hs-button {
          width: 100%;

          &:hover,
          &:focus {
            background-color: $blue-1;
            color: $white;
          }
        }

        .actions {
          width: fit-content;
          @include bs-btn;
          @include btn-download;
          margin-bottom: 0;
          width: fit-content;
          font-family: $redhat;
          font-size: 16px;
          line-height: 20px;
          font-weight: 700;

          &:before {
            background-image: url($ROOT_PATH + 'media/icons/blue-arrow.svg');
          }

          &:hover,
          &:active,
          &:focus-within,
          &:focus-visible {
            border: 1px solid $white;
            input {
              color: $white !important;
            }
            &:before {
              background-image: url($ROOT_PATH + 'media/icons/green-arrow.svg');
            }

            .btn-txt {
              color: #17fcc4;
            }
          }

          .hs-button.primary.large {
            transform: unset !important;
            background-color: unset !important;
            padding: unset !important;
            font-size: unset !important;
          }
        }
        width: fit-content;

        .btn-bg-el,
        .btn-bg-el::before {
          background-color: transparent;
        }
      }
      .hs-email {
        margin-bottom: 18px !important;
        flex: 1;
        max-width: 425px;
      }
    }

    form {
      flex-direction: column;
      margin-top: 39px;
      margin-left: 0;
      width: 90%;
    }
  }

  &.square {
    display: flex !important;
    flex-direction: row-reverse;
    align-items: center;

    .image-block {
      position: relative !important;
      mask: unset !important;
      right: auto !important;
      height: auto !important;
      max-height: none !important;
      flex-shrink: 0;
      width: 40% !important;
      padding: 30px;
      flex-shrink: 1;

      picture {
        height: auto;
        max-height: none;
      }

      img {
        position: relative !important;
        object-fit: contain !important;
        right: auto !important;
      }
    }

    .newsletter-form {
      .hbspt-form form {
        flex-direction: column;
        align-items: flex-start;

        .hs-error-msgs {
          position: relative !important;
        }
      }
    }

    .cta-content,
    .cta-form {
      width: auto;
      max-width: none;
    }
  }
}
